import React, { Component } from 'react';
import './App.css';
import db from './db';

import TodoList from './components/TodoList';
import NewTodoItem from './components/NewTodo';

class App extends Component {
  constructor() {
        super();
        this.state = { todolist: [] };

        // this.state = {
        //     todolist: [{ todo: 'Create Wireframe', done: true }]
        // };
        this.handleAddTodo = this.handleAddTodo.bind(this);
        this.handleDeleteTodo = this.handleDeleteTodo.bind(this);
        this.handleToggleTodo = this.handleToggleTodo.bind(this);

    }

  componentDidMount() {
     db.table('todos')
       .toArray()
       .then((todolist) => {
        //  console.log(todos)
         this.setState({ todolist });
       });
   }
   handleAddTodo(title) {
     console.log(title);
     const todo = {
       title,
       done: false,
     };
     db.table('todos')
       .add(todo)
       .then((id) => {
         const newList = [...this.state.todolist, Object.assign({}, todo, { id })];
         this.setState({ todolist: newList });
       });
   }

   handleToggleTodo(id, done) {
     db.table('todos')
       .update(id, { done })
       .then(() => {
         const todoToUpdate = this.state.todolist.find((todo) => todo.id === id);
         const newList = [
           ...this.state.todolist.filter((todo) => todo.id !== id),
           Object.assign({}, todoToUpdate, { done })
         ];
         this.setState({ todolist: newList });
       });
   }

   handleDeleteTodo(id) {
     db.table('todos')
       .delete(id)
       .then(() => {
         const newList = this.state.todolist.filter((todo) => todo.id !== id);
         this.setState({ todolist: newList });
       });
   }

  render() {
    return (
      <div className="App">
        <header className="App-header">
          <h1 className="App-title">Your Todo List</h1>
        </header>

        <div id="todo-container">

          <NewTodoItem handleAddTodo={this.handleAddTodo} />

          <TodoList
            todolist={this.state.todolist}
            handleToggleTodo={this.handleToggleTodo}
            handleDeleteTodo={this.handleDeleteTodo}
          />

        </div>
        <footer>
          <h6>foobar</h6>
        </footer>
      </div>
    );
  }
}

export default App;



