import React, { Component } from 'react';
import PropTypes from 'prop-types';

class TodoItem extends Component {

  constructor() {
    super();
  }

  render() {

    let todo = this.props.todo, title = this.props.title,
    id = this.props.id, done=this.props.done,
    handleToggleTodo=this.props.handleToggleTodo,
    handleDeleteTodo=this.props.handleDeleteTodo;

    return (
          <li>
              <div>
              <input
                type="checkbox"
                checked={done}
                onChange={(e) => handleToggleTodo(id, !done)}
              />
                <textarea name="" defaultValue={title} onChange={(e) => this.handleChange(e.target.value)}></textarea>
                <button type="button" onClick={() => handleDeleteTodo(id)}>Delete</button>
              </div>
          </li>
    );
  }
}

TodoItem.propTypes = {
  title: PropTypes.string.isRequired,
  id: PropTypes.number.isRequired,
  done: PropTypes.bool,
  handleToggleTodo: PropTypes.func.isRequired,
  handleDeleteTodo: PropTypes.func.isRequired
};

export default TodoItem;



