import React, { Component } from 'react';
import PropTypes from 'prop-types';

class NewTodoItem extends Component {
  constructor() {
    super();
    this.state = {value: ''};
    this.addTodo = this.addTodo.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }

  addTodo() {
    this.props.handleAddTodo(this.state.value);
  }

  handleChange(newValue) {
    this.setState({value: newValue});
  }

  render() {
    return (
      <div className="new-todo-container">
          <textarea name="new-todo" value={this.state.value} onChange={(e) => this.handleChange(e.target.value)}></textarea>
          <div className="new-todo-options"></div>
          <button type="button" onClick={this.addTodo}>Add Todo</button>
      </div>
    );
  }
}

NewTodoItem.propTypes = {
  handleAddTodo: PropTypes.func.isRequired,
};

export default NewTodoItem;



